import {Form,Button,Row,Col,Modal} from 'react-bootstrap';
import {useState,useEffect} from 'react';
import { model } from '../shared/model_data';
import { SheetJSFT } from '../shared/file_types';
import { make_cols } from './MakeColumns';
import axios from "axios";
import XLSX from 'xlsx';
import {useHistory} from "react-router-dom";
import { default as ReactSelect } from "react-select";
import { components } from "react-select";
import {MdAddCircle} from 'react-icons/md';
import SchemeBoxComponent from './SchemeBoxComponent';
// var Multiselect = require('react-bootstrap-multiselect');

function formattedDate(d = new Date) {
    let month = String(d.getMonth() + 1);
    let day = String(d.getDate());
    const year = String(d.getFullYear());
  
    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;
  
    return `${year}-${month}-${day}`;
  }

function NewSchemeForm(){
    const [file,setFile] = useState();
    
    const [fileHeader,setFileHeader] = useState();
    const [userHeader,setUserHeader] = useState({
        date: '',
        model: '',
        price: ''
    });
    const [modelList,setModelList] = useState();
    
    const [modelShow,setModelShow] = useState();
    const [userHeaderSubmit,setUserHeaderSubmit] = useState();
    const [schemeBox,setSchemeBox] = useState();
    const [fileStartDate,setFileStartDate] = useState();
    const [fileEndDate,setFileEndDate] = useState();
    const [schemeNum,setSchemeNum] = useState(0);
    const [schemeArray,setSchemeArray] = useState([]);
    const [excelJSON,setExcelJSON] = useState();

    const history = useHistory();
    var fileHeaderOptions;
    useEffect(() => {
        setFileHeader(null);
        setModelShow(false);
      }, []);
    useEffect(()=>{
        renderSchemeBoxes();
    },[schemeArray]);
    if(fileHeader) fileHeaderOptions = fileHeader.map((head)=>{return (<option key = {head} value={head}>{head}</option>)}); 
    
    function   handleActivationFileChange(e) {
        const files = e.target.files;
        if (files && files[0]) {
            console.log(files[0]);
            setFile(files[0]);
        }
    };
    function   handleActivationUpload(e) {
        e.preventDefault();
        /* Boilerplate to set up FileReader */
        const reader = new FileReader();
        const rABS = !!reader.readAsBinaryString;
        // console.log(file);
        try{
            reader.onload = (e) => {
            /* Parse data */
            const bstr = e.target.result;
            const wb = XLSX.read(bstr, {
                type: rABS ? 'binary' : 'array',
                bookVBA: true,
            });
            /* Get first worksheet */
            const wsname = wb.SheetNames[0];
            const ws = wb.Sheets[wsname];
            /* Convert array of arrays */
            const data = XLSX.utils.sheet_to_json(ws);
            /* Update state */
            //   console.log(JSON.stringify(data, null, 2));
            // const dataJSON = JSON.stringify(data, null, 2);
            setExcelJSON(data);
            setFileHeader(Object.keys(data[0]));
            setModelShow(true);
            //   this.setState({ data: data, cols: make_cols(ws['!ref']) }, () => {
                
            //   });
            };
            if (rABS) {
            console.log('rABS');
            reader.readAsBinaryString(file);
            } else {
            reader.readAsArrayBuffer(file);
            }
        }
        catch{
            alert("Please select Valid file");
        }
      }
    function handleUserHeaderSubmit(e){
        e.preventDefault();
        const curr_state = {
            date: e.target.dateCol.value,
            price: e.target.priceCol.value,
            model: e.target.modelCol.value,
        }
        const Modelcol = e.target.modelCol.value;
        setModelList([...new Set(excelJSON.map(value=>value[Modelcol]))]);
        const start_date = new Date('1900-01-01');
        const end_date = new Date('1900-01-01');
        const dateList = [];
        const dateColumn = e.target.dateCol.value;
        excelJSON.map(value=>dateList.push(value[dateColumn]));
        const min_date = Math.min.apply(null, dateList);
        const max_date = Math.max.apply(null, dateList);
        console.log(min_date);
        console.log(max_date);
        start_date.setDate(start_date.getDate()+min_date-2);
        end_date.setDate(end_date.getDate()+max_date-2);
        setFileStartDate(formattedDate(start_date));
        setFileEndDate(formattedDate(end_date));
        setUserHeader(curr_state);
        setUserHeaderSubmit(true);
        setModelShow(false);
        return(<div></div>);
    }
    function addSchemeBox(e){
        
        e.preventDefault();
        if(!modelList){
            alert("Please upload the activation sheet first");
            return(<div></div>);
        }
        var scheme = schemeArray;
        scheme.push({"schemeNum" : schemeNum,"schemeComponent":<SchemeBoxComponent key={schemeNum} modelList = {modelList} startDate = {fileStartDate} endDate = {fileEndDate} schemeNum = {schemeNum} setSchemeArray={setSchemeArray} schemeArray={schemeArray}/>});
        setSchemeNum(schemeNum+1);
        setSchemeArray(scheme);
        renderSchemeBoxes();
    }
    function renderSchemeBoxes(){
        setSchemeBox(schemeArray.map((scheme)=>{console.log(scheme.schemeComponent);return(scheme.schemeComponent)}));
    }
    async function handleAddScheme2(event){
        event.preventDefault();
        
        console.log(event);
        if(schemeNum === 0){
            alert("Please add a scheme first using + Button");
            return(<div></div>);
        }
        else {
            console.log(schemeArray);
            var schemeName = [];
            var start_date = [];
            var end_date = [];
            var condType = [];
            var price_condition = [];
            var model_condition = [];
            var creditValue = [];
            var modelOptions = [];
            for (let i = 0; i < schemeArray.length; i++) {
                console.log(i);
                var schemeNum = schemeArray[i]["schemeNum"];
                schemeName.push(event.target["schemeName_"+schemeNum].value);
                start_date.push(event.target["startDate_"+schemeNum].value);
                end_date.push(event.target["endDate_"+schemeNum].value);
                condType.push(event.target["condType_"+schemeNum].value);
                if(event.target["condType_"+schemeNum].value === "No"){
                    price_condition.push({"priceOperator" : null,"condValue" : null});
                    model_condition.push(null);
                }
                else if(event.target["condType_"+schemeNum].value === "Price_Condition"){
                    price_condition.push({"priceOperator" : event.target["priceOperator_"+schemeNum].value,"condValue" : event.target["condValue_"+schemeNum].value});
                    model_condition.push(null);
                }
                else if(event.target["condType_"+schemeNum].value === "Model_Condition"){
                    price_condition.push({"priceOperator" : null,"condValue" : null});
                    var modelOptionName = "modelOptionsSelected_" + i;
                    model_condition.push(Object.values(event.target["modelOptionsSelected_" + schemeNum]).map(item=>item.value));
                }
                creditValue.push({"creditType":event.target["creditType_" + schemeNum].value,"creditValue":event.target["creditValue_" + schemeNum ].value});
            }
            try{                   
                await axios.post(`/scheme/`, {
                        name: schemeName,
                        start_date: start_date,
                        end_date: end_date,
                        condition_type: condType,
                        price_condition: price_condition,
                        model_condition: model_condition,
                        data_header:{
                            date: userHeader.date,
                            price: userHeader.price,
                            model: userHeader.model
                        },
                        excel_data:excelJSON,
                        creditValue:creditValue,
                        excel_file:{
                            name:file.name,
                            size:file.size
                        },
                        fileName:file
                }).then(alert('Scheme added succesfully')).then(history.push("/"));
            }
            catch(e){
                console.log(e);
                alert(e.message);
            }
            
        }
    }
    return(
    <div className = "container">
        <div className="row">
            <div className="col-md-4 offset-md-4">

            {/* <h3>Now Understanding Credit Note is Easier, Just upload your activation sheet and scheme Details, credit Note (CN) will be calculated for you in less than a minute</h3> */}


            <p>Please select the Activation file first, then add the scheme details below</p>
            <Form onSubmit = {handleActivationUpload}> 
            <Form.Group controlId="formFile" className="mb-3">
                    <Form.Label>Upload Activation Sheet</Form.Label>
                    <Form.Control type="file" accept={SheetJSFT} onChange={handleActivationFileChange}/>
                    <Button variant="primary" type="submit">Upload</Button>
            </Form.Group>
            </Form>
            {/* <Form onSubmit = {handleImageFileChange}> 
            <Form.Group controlId="formFile" className="mb-3" >
                    <Form.Label>Upload Scheme Image (Optional)</Form.Label>
                    <Form.Control type="file" name="file" />
            </Form.Group>
            </Form> */}
        <Form onSubmit={(e) => {handleAddScheme2(e)}}>
            {/* <Form.Group controlId="formFile" className="mb-3" >
                    <Form.Label>Upload Scheme Image (Optional)</Form.Label>
                    <Form.Control type="file" name="file" onChange={handleImageFileChange}/>
            </Form.Group> */}
            <div className="scheme-wrapper">
                {schemeBox}
            </div>
            <Button  type="button" onClick={(e) => {addSchemeBox(e);}} className="add-multi-scheme"><MdAddCircle size={40} /></Button>


                <Button variant="primary" type="submit">
                    Submit
                </Button>
                
            </Form>
        
        <Modal show={modelShow}>
            <Modal.Header>
              <Modal.Title>Please Select the Correct Attributes</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form onSubmit={handleUserHeaderSubmit}>
                    <Form.Group className="mb-3" controlId="date_column">
                        <Form.Label>Date Column</Form.Label>
                        <Form.Control as="select" name="dateCol">
                        {fileHeaderOptions}
                        </Form.Control>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="model_column">
                        <Form.Label>Model Column</Form.Label>
                        <Form.Control as="select" name = "modelCol">
                        {fileHeaderOptions}
                        </Form.Control>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="Price_column">
                        <Form.Label>Price Column</Form.Label>
                        <Form.Control as="select" name ="priceCol">
                        {fileHeaderOptions}
                        </Form.Control>
                    </Form.Group>
                    <Modal.Footer>
                        <Button variant="primary" type="Submit">
                            Submit 
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal.Body>
            
          </Modal>
        </div>
        </div>
    </div>)
}
export default NewSchemeForm;